<script>
// import NameHandler from "./components/NoFocusHide.vue";
// 兼容方法，获取浏览器scrollTop
function getScrollTop() {
  // console.log('chrome浏览器',document.documentElement.scrollTop);
  // console.log('ie浏览器',document.body.scrollTop);
  // window.pageYOffset不能放在最后，因为其返回值为undefined
  return (
    document.documentElement.scrollTop ||
    window.pageYOffset ||
    document.body.scrollTop
  );
}
export default {
  name: "app",
  components: {},
  data: function() {
    return {
      show: false,
      buttonClicked: false,
      mouseEnterLeft: false,
      mouseEnterRight: false
    };
  },
  methods: {
    //   通过处理鼠标进入事件更改hover的逻辑属性
    enterHandler(which) {
      if ("left" === which) {
        this.mouseEnterLeft = true;
      } else if ("right" === which) {
        this.mouseEnterRight = true;
      }
    },
    leaveHandler(which) {
      if ("left" === which) {
        this.mouseEnterLeft = false;
      } else if ("right" === which) {
        this.mouseEnterRight = false;
      }
    },
    handleNav(method) {
      // console.log('开始进行导航栏的隐藏/显示操作');
      let sidebar = document.getElementById("sidebar");

      if (method == "click") {
        this.show = !this.show;
        this.buttonClicked = true;
      } else if (method == "clickoutside") {
        if (this.buttonClicked) {
          this.buttonClicked = false;
        } else {
          this.show = false;
        }
      } else if (method == "scroll") {
        this.show = false;
      } else if (method == "resize") {
        // console.log('resize');

        this.show = false;
      }
      if (this.show) {
        sidebar.className += " show";
        // console.log(sidebar.className);
      } else {
        sidebar.className = "overlay-container";
        // console.log(sidebar.className);
      }
    },
    scrollToID(id) {
      let targetEl = document.getElementById(id);

      // console.log();
      let top = targetEl.getBoundingClientRect().top + getScrollTop();
      window.scroll({ top: top, left: 0, behavior: "smooth" });
    }
  },
  directives: {
    clickoutside: {
      bind: function(el, binding) {
        function documentHandler(e) {
          // console.log('点击事件');

          if (el.contains(e.target)) {
            // console.log('点击了组件内的内容');

            return false;
          }
          if (binding.expression) {
            // console.log('点击了组件外的内容，请求判断');

            binding.value("clickoutside");
          }
        }

        function scrollHandler() {
          if (binding.expression) {
            binding.value("scroll");
          }
        }
        function resizeHandler() {
          // console.log('resize');

          if (binding.expression) {
            binding.value("resize");
          }
        }

        el.__vueClickOutside__ = documentHandler;
        el.__vueScrollOutside__ = scrollHandler;
        document.addEventListener("click", documentHandler);
        document.addEventListener("scroll", scrollHandler);
        window.addEventListener("resize", resizeHandler);
      },
      unbind: function(el) {
        delete el.__vueClickOutside__;
        delete el.__vueScrollOutside__;
      }
    }
  }
};
</script>

